<template>
	<div id="form">
	    <div id="frmContainer">
	    	<component v-bind:is="currentView" ref="component"></component>
	    </div>
	    <div id="footer">
	    	<yd-tabbar slot="tabbar">
	    	  <tabbar-item v-for="(item,index) in buttons" :key="index" 
	    	  	:title="item.title" name="item.name" type="a"  
	    	  	:icon="item.icon"  v-on:handclick="handclick(item.name)"  :active="item.active"></tabbar-item>
	    	</yd-tabbar>
	    </div>
	    <div class="wrapper" id="wrapper"></div>
    </div>
</template>

<script>

import Vue from 'vue'
import {RxUtil} from "@/assets/util.js";
import {customFormComponet} from '@/assets/app.js';
import TabItem from '@/components/common/TabItem.vue';
import TabPanel from '@/components/common/TabPanel.vue';
import RxTab from '@/components/common/RxTab.vue';
import router from '@/router/index';
import {openDialogByAlias} from '@/assets/CustomDialog.js';

import RxInput from '@/components/common/form/RxInput';
import RxSpiner from '@/components/common/form/RxSpiner';
import RxTextArea from '@/components/common/form/RxTextArea';
import RxCheckBoxList from '@/components/common/form/RxCheckBoxList';
import RxCombobox from '@/components/common/form/RxCombobox';
import RxRadioButtonList from '@/components/common/form/RxRadioButtonList';
import RxComplex from '@/components/common/form/RxComplex';
import RxDate from '@/components/common/form/RxDate';
import RxAttachment from '@/components/common/form/RxAttachment';
import RxCheckbox from '@/components/common/form/RxCheckbox';
import SysUser from '@/components/common/SysUser';
import SysGroup from '@/components/common/SysGroup';
import RxUser from '@/components/common/form/RxUser';
import RxGroup from '@/components/common/form/RxGroup';
import RxMonth from '@/components/common/form/RxMonth';
import RxTime from '@/components/common/form/RxTime';
import RxButtonEdit from '@/components/common/form/RxButtonEdit';

import {Input} from 'vue-ydui/dist/lib.rem/input';
import RxSearch from '@/components/common/RxSearch';
import NoData from '@/components/common/NoData';
import RxSearchPanel from '@/components/common/RxSearchPanel';
import rxRadio from '@/components/common/rxRadio';
import rxCheckbox from '@/components/common/rxCheckbox';
import RxBtnEdit from '@/components/common/RxBtnEdit.vue';

Vue.component(TabItem.name, TabItem);
Vue.component(TabPanel.name, TabPanel);
Vue.component(RxTab.name, RxTab);

Vue.component(RxInput.name, RxInput);
Vue.component(RxSpiner.name, RxSpiner);
Vue.component(RxTextArea.name, RxTextArea);
Vue.component(RxCheckbox.name, RxCheckbox);
Vue.component(RxCheckBoxList.name, RxCheckBoxList);
Vue.component(RxCombobox.name, RxCombobox);
Vue.component(RxRadioButtonList.name, RxRadioButtonList);
Vue.component(RxComplex.name, RxComplex);
Vue.component(RxDate.name, RxDate);
Vue.component(RxAttachment.name, RxAttachment);
Vue.component(RxUser.name, RxUser);
Vue.component(RxGroup.name, RxGroup);
Vue.component(SysUser.name, SysUser);
Vue.component(SysGroup.name, SysGroup);
Vue.component(RxMonth.name, RxMonth);
Vue.component(RxTime.name, RxTime);
Vue.component(RxButtonEdit.name, RxButtonEdit);

// 引用对话框
Vue.component(RxSearch.name, RxSearch);
Vue.component(RxSearchPanel.name, RxSearchPanel);
Vue.component(NoData.name, NoData);
Vue.component(Input.name, Input);
Vue.component(rxRadio.name, rxRadio);
Vue.component(rxCheckbox.name, rxCheckbox);
Vue.component(RxBtnEdit.name, RxBtnEdit);

/**
 * 加载表单
 */
var loadingComponent = {
	  template: "<div>正在加载自定义表单</div>"
}

export default {
	name : "CustomFormEdit",
	data() {
		return {
			boKey:"",
	    	alias:"",
	    	id:"",
	    	formVueInst:{},
	    	currentView: loadingComponent,
	    	
	    	buttons:[
	    		{
	    			title:"启动",
	    			name:"handStart",
	    			icon:"yd-icon-ok",
	    			active:true
	    		},
	    		{
	    			title:"确定",
	    			name:"handSave",
	    			icon:"yd-icon-save",
	    			active:true
	    		},
	    		{
	    			title:"取消",
	    			name:"cancel",
	    			icon:"yd-icon-cancel",
	    			active:false
	    		}
	    	]
	   }
	},
  	mounted(){
		this.boKey=this.$route.params.boKey;
		this.alias=this.$route.params.alias;
		this.id=this.$route.params.id;
		this.readonly=this.$route.params.readonly;
		
		//解决跳转的问题。
  		this.id=(this.id=="-1")?"":this.id;
  		
		var url=_baseUrl +"/mobile/custom/form/"+this.alias+"/"+this.id+".do";
		
		this.$dialog.loading.open('正在加载表单...');
		this.$ajax.get(url).then(response => {
			this.$dialog.loading.close();
			var rtn=response.data;
			
			if(!rtn.result){
				this.errorToast(rtn.msg);
				return ;
			}
			var canStartFlow=rtn.canStartFlow;
			var formModel=rtn.formModel;
			var formHtml=formModel.content;
			var htmlObj=RxUtil.parseHtml(formHtml);
			var html=htmlObj.html;
			html=html.replaceAll("&#39;","'").replaceAll("&amp;","&");
			
			customFormComponet.options.template=`<div >${html}</div> `;
			//插入脚本
			RxUtil.insertScript("frmContainer",htmlObj.script);
			//设置组件
			this.currentView=customFormComponet;
			
			this.$nextTick(function(){
				this.formVueInst=this.$refs.component;
				if("CustomForm"==this.formVueInst.constructor.extendOptions.name){
					this.init(formModel);
				}
			});
			//不启动流程
			if(!canStartFlow){
				this.buttons.splice(0,1);
			}
			//添加自定义按钮。			
			this.constructButton();
		});
	},
	methods:{
		init(formModel){
			var json=formModel.jsonData;
			this.handSubData(json)
			this.formVueInst.data=json;
			this.formVueInst.permission=formModel.permission? eval('(' + formModel.permission + ')'):{};
			this.formVueInst.readonly=this.readonly;
		},
		handSubData(formData){
			for(var key in formData){
				if(!key.startWith("SUB_")) continue;
				var data=formData[key];
				for(var i=0;i<data.length;i++){
					data[i].selected=false;
				}
			}		
		},
		//调用表单自定义方法。
		handclick(click){
			this["func_" +click](this.formVueInst);
		},
		handOk(action){
			var setting={alias:this.alias,action:action};
			var formData=JSON.stringify(this.formVueInst.data);
			var data={formData:formData,setting:setting};
			var url=_baseUrl +"/sys/customform/sysCustomFormSetting/saveData.do";
			this.$ajaxJson.post(url,data).then(res=>{
				var rtn=res.data;
				if(rtn.success){
					this.successToast(rtn.message,500,"customList",{alias:this.boKey}); 
				}
				else{
					this.errorToast(rtn.message);
				}
			})
		},
		func_handSave(){
			this.handOk("save");
		},
		func_handStart(){
			this.handOk("startFlow");
		},
		func_cancel(){
			this.goBack()
		},
		constructButton(){
			/*buttonFuntions 格式:[{
				{
	    			title:"确定",
	    			name:"handSave",
	    			icon:"yd-icon-save",
	    			active:true,
	    			action:function(data){
	    				
	    			}
	    		}
			}]
			var buttonFuntions=[...];
			*/
			if(window.buttonFuntions) {
				for(var i=0;i<buttonFuntions.length;i++){
					var json=buttonFuntions[i];
					this["func_" +json.name]=json.action;
					delete json.action;
					this.buttons.push(json);
				}	
			}
			//给vue 实例增加方法。
			/*custFuntions 格式:[{
				{
	    			name:"handSave",
	    			action:function(vueInst){
	    			}
	    		}
			}]
			var custFuntions=[...];
			*/
			if(window.custFuntions){
		    	for(var i=0;i<custFuntions.length;i++){
					var json=custFuntions[i];
					this["func_" +json.name ]=json.action;
				}	
		    }
		}
		
		
	}
}
</script>

<style scoped>
	#form{
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		display: flex;
		flex-direction:column;
	}
	
	#frmContainer{
		flex:1;
		overflow-y: auto;
    	overflow-x: hidden;
	}
	
	#footer{
		height: 1rem;
		width: 100%;
		display: flex;
		align-content: center;
		background: #F5F5F5;
	}
	
</style>


