<template>
	<div class="yd-search" >
    <div class="yd-search-input">
    		<div class="search-input">
    			<i class="search-icon"></i>
            <yd-input
                    type="search"
                    placeholder="搜索"
                    v-model="currentValue"
                    ref="search" :on-blur="blur"
            ></yd-input>
    		</div>
        <a href="javascript:;" class="cancel-text" v-show="senior" @click="showSenior" >高级</a>
    </div>
   </div>
</template>

<script>
	 export default {
        name: 'rx-search',
        props: {
            value: String,
            senior:true
        },
        data(){
        	return {
        		currentValue:""
        	}
        },
        methods:{
        	blur(e){
        		this.$emit("input",this.currentValue);
        	},
        	showSenior(){
        		this.$emit("showsenior",this)
        	}
        },
        created(){
        	this.currentValue=this.value;
        }
        
 }
</script>

<style scoped>
</style>


