<template>
	<div v-if="right=='r'" class="readOnlyBgColor">{{value}}</div>
	<div class="box-custom-component" v-else-if="right=='w'">
	    <group   label-width="5em" v-if="right=='w'">
	      <popup-picker  :data="data" v-model="selectVal" 
	      	 @on-change="onChange" :placeholder="请选择" :display-format="format"></popup-picker>
	    </group>
	</div>
</template>



<script>
import { PopupPicker, Group, Cell } from 'vux';
import { calcRight } from "@/assets/app.js";

export default {
	name : "rx-month",
  components: {
    PopupPicker,
    Group,
    Cell
  },
  props: {
  	title:String,
  	showDefault:{
  		  type: Boolean,
        default: true
  	},
  	value:String,
		permission:Object,
		permissionkey:String,
		readonly: {
	        type: Boolean,
	        default: false
    	}
  },
  methods: {
    onChange (val) {
    	var v=this.format(val);
    	this.selectVal=val;
    	this.$emit("input",v);
    },
    getDeault(){
    	var date=new Date();
    	var year=date.getFullYear(); 
 			var month=date.getMonth()+1;
 			var rtn=[];
 			rtn.push(year +"");
 			rtn.push(month +"");
 			return rtn;
    },
    format(value, name) {
        return `${value[0]}-${value[1]}`;
    },
    init(){
    	var years=[];
    	var months=[];
    	for(var year=1970;year<=2030;year++){
    		years.push(year +"");
    	}
    	for(var mon=1;mon<=12;mon++){
    		months.push(mon +"");
    	}
    	this.data.push(years);
    	this.data.push(months);
    }
    
  },
  data () {
    return {
      data: [],
      selectVal:[]
    }
  },
  computed: {
    	right :function () {
				return calcRight(this);	
    	}
  },
  created(){
    	this.init();
    	
    	if(this.value){
    		var ary=this.value.split("-");
    		this.selectVal=ary;
    	}
    	else{
    		if(this.showDefault){
    			var v=this.getDeault();
    			this.selectVal=v;
    		}
    	}
    	
    	
  }
}
</script>

<style scoped scoped>
.picker-buttons {
  margin: 0 15px;
}
</style>


