import { RxUtil } from "@/assets/util.js";
import { calcRight } from "@/assets/app.js";

export default {
    name: "rx-checkbox",
    props: {
        permission: Object,
        permissionkey: String,
        conf: String,
        name: String,
        data: Object,
        readonly: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            checked:"",
    		checkReq:true
        }
    },
    computed: {
        right: function() {
           return calcRight(this);
        }
    },
    mounted() {
    	if(!this.conf||this.right=='r') return;
        this.checked = JSON.parse(this.conf).checked;
    },
    methods: {
        change() {
        	
            var inputCheckbox = this.$refs.input;
            var config=JSON.parse(this.conf);
            if(inputCheckbox.checked){
                this.data[this.name]=config.truevalue;
            } else{
                this.data[this.name]=config.falsevalue;
            }
        	this.checked=inputCheckbox.checked;
        },
    }

}



