
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { calcRight,componentAjaxJson } from "@/assets/app.js";

export default {
name: "rx-radiobuttonlist",
components:{
Selector,
Group
},
props: {
permission: Object,
permissionkey: String,
conf: String,
name: String,
data: Object,
readonly: {
type: Boolean,
default: false
},
required: {
type: Boolean,
default: false
},
},
data() {
return {
selectJson: "",
checkVal:""
}
},
computed: {
right: function() {
return calcRight(this);
},
},
mounted() {
if(!this.conf||this.right=='r') return;
var conf = JSON.parse(this.conf);
var from = conf.from;
var params={};
if(from == 'dic') {
var dic = conf.dic;
params = {dic: dic};
componentAjaxJson(conf,params,this,data=>{
this.selectJson=this.changeAttr("key", "value", "key", "value", data);
});
} else if(from == 'self') {//字典是key value形式  不需要转换
var data = conf.data;
this.selectJson = this.changeAttr("key", "name", "key", "value", data);
} else if(from == 'sql'||from=='url') {
componentAjaxJson(conf,params,this,data=>{
var textfield = conf.textfield;
var valuefield = conf.valuefield;
this.selectJson=this.changeAttr(valuefield, textfield, "key", "value",data);
});

}
},
methods: {

changeAttr(valueField, textField, key, value, data) {
var ary = [];
for(var i = 0; i < data.length; i++) {
var ob = {};
ob[key] = data[i][valueField]; 
ob[value] = data[i][textField];
if(this.data[this.name] == ob[key]) {
ob.checked = true;

}
else{
ob.checked = false;
}
ary.push(ob);
}
return ary;
},
onChange(key) {
var selectJson = this.selectJson;
for(var i = 0; i < selectJson.length; i++) {
if(key == selectJson[i].key) {
this.data[this.name] = key; 
this.data[this.name + '_name'] = selectJson[i].value; 

break;
}
}
},
valid(chkReq_){
if(chkReq_&& this.required){
if(this.data[this.name]){
return true;
} else {
return false;
}
} else{
return true;
}

}
}

}



