<template>
	<div v-if="right=='r'" class="readOnlyBgColor">{{data[name+'_name']}}</div>
	<div class="box-custom-component" v-else-if="right=='w'">
		<group>
			<selector 
				:options="selectJson" 
				v-model="data[name]" 
				@on-change="onChange" 
				v-if="selectJson"
			></selector>
		</group>
		<!--<span class="yd-icon-must" v-show="required&&emptyVal"></span>-->
	</div>
</template>

<script type="text/ecmascript-6">
	import { Selector, Group } from 'vux';
	import { calcRight, componentAjaxJson } from "@/assets/app.js";

	export default {
		components: {
			Group,
			Selector
		},
		name: "rx-combobox",
		props: {
			permission: Object,
			permissionkey: String,
			conf: String,
			name: String,
			data: Object,
			readonly: {
				type: Boolean,
				default: false
			},
			required: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				selectJson: "",
				parentField: "",
				linkField:"",
				emptyVal:false
			}
		},
		computed: {
			right: function() {
				return calcRight(this);
			}
		},
		mounted() {
			if(!this.conf || this.right == 'r') return;
			var conf = JSON.parse(this.conf);
			var from = conf.from;
			var params = {};
			if(from == 'dic') {
				var dic = conf.dic;
				params = {
					dic: dic
				};
				componentAjaxJson(conf, params, this, data => {
					this.selectJson = data;
				}); //字典是key value形式  不需要转换
			} else if(from == 'self') {
				var data = conf.data;
				this.selectJson = this.changeAttr("key", "name", "key", "value", data);
			} else if(from == 'sql' || from == 'url') {
				var sqlKey = conf.sql;
				var sql_params = conf.sql_params;
				var sql_parent =conf.sql_parent;
				var param = {};
				if(sql_params&&sql_parent) { //父联动字段
					this.parentField = sql_parent;
					this.linkField   =sql_params;
					if(!this.data[sql_parent]) { //父联动字段为空
						param[sql_params] = "---";
					} else {
						param[sql_params] = this.data[sql_parent];
					}
				}
				params = {
					params: param
				};
				componentAjaxJson(conf, params, this, data => {
					var textfield = conf.textfield;
					var valuefield = conf.valuefield;
					this.selectJson = this.changeAttr(valuefield, textfield, "key", "value", data);
				});
			}
			if(this.parentField) {
				this.$watch("data." + this.parentField, function(val, oldVal) {
					var param = {};
					param[this.linkField]=val;
					params = {
						params: param
					};
					var textfield = conf.textfield;
					var valuefield = conf.valuefield;
					componentAjaxJson(conf, params, this, data => {
						this.selectJson = this.changeAttr(valuefield, textfield, "key", "value", data);
					});
				});
			}
			
			if(this.data[this.name]){
				this.emptyVal=false;
			} else{
				this.emptyVal=true;
			}
		},

		methods: {
			onChange(key) {
				var selectJson = this.selectJson;
				for(var i = 0; i < selectJson.length; i++) {
					if(key == selectJson[i].key) {
						this.data[this.name] = key; //实际值
						this.data[this.name + '_name'] = selectJson[i].value; //显示值
						this.emptyVal=false;
						break;
					}
				}

			},
			//属性转换
			changeAttr(valueField, textField, key, value, data) {
				var array = [];
				for(var i = 0; i < data.length; i++) {
					var ob = {};
					ob[key] = data[i][valueField]; //key是实际值 value是本文值
					ob[value] = data[i][textField];
					array.push(ob);
				}
				return array;
			},
			valid(chkReq_){
				if(chkReq_&& this.required){
					if(this.emptyVal){
						return false;
					} else {
						return true;
					}
				} else{
					return true;
				}
			}
			
		}

	}
</script>

<style scoped>
.box-custom-component >>> .weui-cells::before{
	border-top: none;
}

.box-custom-component .weui-select{
	height: auto;
}
</style>


