<template>
	<div v-if="right=='r'" class="readOnlyBgColor">{{display}}</div>
	<div class="box-custom-component" v-else-if="right=='w'">
		<rx-boxs v-model="group" containstyle="width:2.2rem;" textField="text" 
			@select="selectGroupDialog" valField="id" :single="single"></rx-boxs>	
	</div>
</template>

<script>
import rxboxs from '@/components/common/rx-boxs';
import { calcRight } from "@/assets/app.js";

import Vue from 'vue';
Vue.component(rxboxs.name, rxboxs);
	
export default{
    name : "rx-group",
    props: {
    	name:String,
    	data:Object,
    	permission:Object,
    	permissionkey:String,
    	single:{
    		type: Boolean,
            default: false
    	},
    	readonly: {
            type: Boolean,
            default: false
        },
       	required: {
            type: Boolean,
            default: false
        }
    },
    data(){
    	return {
    		group:'',
    		rxBoxVm:{}
    	}
    	
    },computed: {
    	right :function () {
			return calcRight(this);	
    	},
    	display :function(){
    		return this.data[this.name +"_name"];
    	}
    },
    methods: {
    	//打开对话框。
    	selectGroupDialog(vm){
    		//发布事件。
	      	this.$emit("opengroupdialog",this);
    	},
    	/**
    	 * 设置选中的用户。
    	 */
    	setGroup(aryGroup){
    		if(!aryGroup || aryGroup.length==0) {

    			this.changeData("","");
    			return;
    		}
    		var groups=[];
    		
    		var aryId=[];
    		var aryName=[];
    		for(var i=0;i<aryGroup.length;i++){
    			var group=aryGroup[i];
    			//fullname
    			var o={};
    			o.id=group.groupId;
    			o.text=group.name;
    			groups.push(o);
    			
    			aryId.push(group.groupId);
    			aryName.push(group.name);
    		}
    		this.group=JSON.stringify(groups);
    		
    		this.changeData(aryId.join(","),aryName.join(","));
    	},
    	/**
    	 * 修改对应的数据。
    	 */
    	changeData(groupIds,names){
    		this.data[this.name]=groupIds;
    		this.data[this.name +"_name"]=names;
    	},
    	getGroupJson(){
    		var ids=this.data[this.name];
    		if(!ids) return '';
    		var names=this.data[this.name +"_name"];
    		var aryIds=ids.split(",");
    		var aryNames=names.split(",");
    		var aryGroup=[];
    		for(var i=0;i<aryIds.length;i++){
    			var o={};
    			o.id=aryIds[i];
    			o.text=aryNames[i];
    			aryGroup.push(o);
    		}
    		return JSON.stringify(aryGroup);
    	},
    	valid(chkReq_){
    		if(chkReq_&& this.required){
    			if(this.group&&this.group!='[]'){
	    			return true;
    			} else {
	    			return false;
    			}
    		} else {
    			return true;
    		}
    	}
    },
    created(){
    	var groupJson=this.getGroupJson();
    	this.group=groupJson;
    }
}

</script>

<style scoped>
	.div-group{
		margin-top:.05rem;
	}
</style>


