<template>
	<div v-if="right=='r'" class="readOnlyBgColor">{{currentValue}}</div>
	<div class="box-custom-component" v-else-if="right=='w'">
		 <yd-spinner :min="min" :max="max" :unit="unit" v-model="currentValue" ></yd-spinner>
		 <!--<span class="yd-icon-must" v-if="required&&iserror"></span>-->
	</div>
</template>

<script>

import { calcRight } from "@/assets/app.js";
import {VTypes,RxUtil} from "@/assets/util.js";

export default{
    name : "rx-spiner",
    props: {
    	value:[Number,String],
    	permission:Object,
    	permissionkey:String,
    	readonly: {
            type: Boolean,
            default: false
        },
//		required: {
//     	    type: Boolean,
//          default: false
//  	},
        unit:{
            type: Number,
            default: 1
        },
        max: {
            type: Number,
            default: 100000000
        },
        min: {
            type: Number,
            default: 0
        }
    },
    data(){
    	return {
    		iserror:false,
    		currentValue:0
    	}
    },
    computed: {
    	right :function () {
			return calcRight(this);	
    	}
    },
    mounted(){
    	if(!this.value){
    		this.currentValue=0;
    	};
    	this.currentValue=this.value;
		//this.valid(this.required);
    },
    methods: {
//		valid(chkReq_) {
//			var val=this.value;
//			if(chkReq_ && this.required){
//				if(!val||val.toString().trim()==""){
//					this.iserror=true;
//					return false;
//				} else{
//					var validFunc=VTypes.isNumber;
//					var rtn= validFunc.valid(val);
//					if(rtn){
//						this.iserror=false;
//						return true;
//					} else {
//						this.iserror=true;
//						return false;
//					}
//				}
//			} else {
//				return true;
//			}
//		}
		
	},
    watch: {
    	value: function (val, oldVal){
    		this.currentValue=val;
		},
		currentValue: function (val, oldVal){
    		this.$emit('input', val);
//			this.iserror=false;
		}
    }
}

</script>

<style scoped>
	.box-custom-component > input{
		height: .6rem;
		padding-left: .12rem;
		color: #666;
		width: 100%;
	}
	
	.box-custom-component >>> .yd-spinner{
		border: none;
	}
	
	.box-custom-component >>> .yd-spinner a::after{
		font-size: .3rem;
		line-height: 25px;
		text-align: center;
	}
	
</style>


