<template>
  <div class="yd-tab-panel-item" :id="panelId()" :class="classes">
    <slot></slot>
  </div>
</template>

<script type="text/babel">
    export default {
        name: 'tab-panel',
        props: {
            label: String,
            active: Boolean,
            tabkey: [String, Number]
        },
        computed: {
            classes() {
                return this.$parent.activeIndex == this._uid ? 'tab-active' : '';
            }
        },
        methods:{
          panelId(){
            return "panel_"+ this.tabkey;
          }
        },
        watch: {
            active() {
                this.$parent.init(true);
            },
	          label() {
                this.$parent.init('label')
	          }
        }
    }
</script>



