import { calcRight } from "@/assets/app.js";
import {VTypes,RxUtil} from "@/assets/util.js";

export default{
    name : "rx-spiner",
    props: {
    	value:[Number,String],
    	permission:Object,
    	permissionkey:String,
    	readonly: {
            type: Boolean,
            default: false
        },
//		required: {
//     	    type: Boolean,
//          default: false
//  	},
        unit:{
            type: Number,
            default: 1
        },
        max: {
            type: Number,
            default: 100000000
        },
        min: {
            type: Number,
            default: 0
        }
    },
    data(){
    	return {
    		iserror:false,
    		currentValue:0
    	}
    },
    computed: {
    	right :function () {
			return calcRight(this);	
    	}
    },
    mounted(){
    	if(!this.value){
    		this.currentValue=0;
    	};
    	this.currentValue=this.value;
		//this.valid(this.required);
    },
    methods: {
//		valid(chkReq_) {
//			var val=this.value;
//			if(chkReq_ && this.required){
//				if(!val||val.toString().trim()==""){
//					this.iserror=true;
//					return false;
//				} else{
//					var validFunc=VTypes.isNumber;
//					var rtn= validFunc.valid(val);
//					if(rtn){
//						this.iserror=false;
//						return true;
//					} else {
//						this.iserror=true;
//						return false;
//					}
//				}
//			} else {
//				return true;
//			}
//		}
		
	},
    watch: {
    	value: function (val, oldVal){
    		this.currentValue=val;
		},
		currentValue: function (val, oldVal){
    		this.$emit('input', val);
//			this.iserror=false;
		}
    }
}




