<template>
	<div v-if="right=='r'" class="readOnlyBgColor">{{display}}</div>
	<div class="box-custom-component" v-else-if="right=='w'">
		<rx-boxs 
			v-model="user" 
			containstyle="width:2.2rem;" 
			textField="text" 
			@select="selectUserDialog" 
			valField="id" 
			:single="single"
		></rx-boxs>	
	</div>
</template>

<script>
import rxboxs from '@/components/common/rx-boxs';
import { calcRight } from "@/assets/app.js";

import Vue from 'vue';
Vue.component(rxboxs.name, rxboxs);
	
export default{
    name : "rx-user",
    props: {
    	name:String,
    	data:Object,
    	permission:Object,
    	permissionkey:String,
    	single:{
    		type: Boolean,
            default: false
    	},
    	readonly: {
            type: Boolean,
            default: false
        },
     	required: {
            type: Boolean,
            default: false
        }
    },
    data(){
    	return {
    		user:'',
    		rxBoxVm:{}
    	}
    	
    },computed: {
    	right :function () {
			return calcRight(this);	
    	},
    	display :function(){
    		return this.data[this.name +"_name"];
    	}
    },
    methods: {
    	//打开对话框。
    	selectUserDialog(vm){
    		//发布事件。
	      	this.$emit("openuserdialog",this);
    	},
    	/**
    	 * 设置选中的用户。
    	 */
    	setUser(aryUser){
    		if(!aryUser || aryUser.length==0) {
    			this.user='';
    			this.changeData("","");
    			return;
    		}
    		var users=[];
    		
    		var aryId=[];
    		var aryName=[];
    		for(var i=0;i<aryUser.length;i++){
    			var user=aryUser[i];
    			//fullname
    			var o={};
    			o.id=user.userId;
    			o.text=user.fullname;
    			users.push(o);
    			
    			aryId.push(user.userId);
    			aryName.push(user.fullname);
    		}
    		this.user=JSON.stringify(users);
    		
    		this.changeData(aryId.join(","),aryName.join(","));
    	},
    	/**
    	 * 修改对应的数据。
    	 */
    	changeData(userIds,names){
    		this.data[this.name]=userIds;
    		this.data[this.name +"_name"]=names;
    	},
    	getUserJson(){
    		var ids=this.data[this.name];
    		if(!ids) return '';
    		var names=this.data[this.name +"_name"];
    		var aryIds=ids.split(",");
    		var aryNames=names.split(",");
    		var aryUser=[];
    		for(var i=0;i<aryIds.length;i++){
    			var o={};
    			o.id=aryIds[i];
    			o.text=aryNames[i];
    			aryUser.push(o);
    		}
    		return JSON.stringify(aryUser);
    	},
    	valid(chkReq_){
    		if(chkReq_&& this.required){
    			if(this.user&&this.user!='[]'){
	    			return true;
    			} else {
	    			return false;
    			}
    		} else {
    			return true;
    		}
    	}
    	
    },
    created(){
    	var userJson=this.getUserJson();
    	this.user=userJson;
    	
    	this.$watch("data." + this.name,function(val,oldVal){
    		var userJson=this.getUserJson();
			this.user=userJson;
    	});
    	
    	this.$watch("data." + this.name +"_name",function(val,oldVal){
    		var userJson=this.getUserJson();
    		
			this.user=userJson;
    	})
    }
}

</script>

<style scoped>

</style>


