import rxboxs from '@/components/common/rx-boxs';
import { calcRight } from "@/assets/app.js";

import {Input} from 'vue-ydui/dist/lib.rem/input';
import RxSearch from '@/components/common/RxSearch';
import NoData from '@/components/common/NoData';
import RxSearchPanel from '@/components/common/RxSearchPanel';
import rxRadio from '@/components/common/rxRadio';
import rxCheckbox from '@/components/common/rxCheckbox';
import TabItem from '@/components/common/TabItem.vue';

import {openDialogByAlias} from '@/assets/CustomDialog.js';

import Vue from 'vue';
Vue.component(rxboxs.name, rxboxs);

Vue.component(RxSearch.name, RxSearch);
Vue.component(RxSearchPanel.name, RxSearchPanel);
Vue.component(NoData.name, NoData);
Vue.component(Input.name, Input);
Vue.component(rxRadio.name, rxRadio);
Vue.component(rxCheckbox.name, rxCheckbox);
Vue.component(TabItem.name, TabItem);
	
export default{
    name : "rx-buttonedit",
    props: {
    	name:String,
    	data:Object,
    	permission:Object,
    	permissionkey:String,
    	conf:String,
    	readonly: {
            type: Boolean,
            default: false
        },
    	required: {
            type: Boolean,
            default: false
       }
    },
    data(){
    	return {
    		jsonData:'',
    		rxBoxVm:{},
    		confJson:{},
    		returnFields:{}
    	}
    	
    },computed: {
    	right :function () {
			return calcRight(this);	
    	},
    	display :function(){
    		return this.data[this.name +"_name"];
    	}
    },
    methods: {
    	//打开对话框。
    	showDialog(){
    		var binding=this.confJson.binding;
    		var dialog=binding.dialogalias;
    		
    		var params=this.getInputParams(binding);
    		
			var txtField=binding.textField;
			var valueField=binding.valueField;
    		openDialogByAlias(dialog,params,data=>{
				var aryJson=[];
				//[{id:"",name:""}]
				for(var i=0;i<data.length;i++){
					var tmp=data[i];
					for(var key in this.returnFields){
						var val=tmp[key];
						//映射数据到其他字段
						var bindField=this.returnFields[key];
						this.data[bindField]=(i==0)?  val:this.data[bindField]+"," + val;
					}
					var o={};
					o.id=tmp[valueField];
					o.name=tmp[txtField];
					aryJson.push(o);
				}
				this.setData(aryJson);
			});
    	},
    	getInputParams(binding){
    		var aryInput=binding.gridInput;
    		if(aryInput.length==0) return null;
    		//{FULLNAME_:"郭靖"}
    		var params={};
    		for(var i=0;i<aryInput.length;i++){
    			var item=aryInput[i];
    			if(item.mode!="mapping") continue;
    			var fieldName=item.fieldName;
    			var val=this.data[fieldName];
    			if(!val) continue;
    			params[fieldName]=val;
    		}
    		return params;
    	},
    	/**
    	 * 数据结构
    	 * [{id:"",name:""},{id:"",name:""}] 
    	 */
    	setData(aryJson){
    		if(!aryJson || aryJson.length==0) {
    			this.jsonData='';
    			this.changeData("","");
    			return;
    		}
    		
    		var aryId=[];
    		var aryName=[];
    		var aryTmp=[];
    		for(var i=0;i<aryJson.length;i++){
    			var obj=aryJson[i];
    			//fullname
    			var o={};
    			o.id=obj.id;
    			o.text=obj.name;
    			aryTmp.push(o);
    			
    			aryId.push(obj.id);
    			aryName.push(obj.name);
    		}
    		this.jsonData=JSON.stringify(aryTmp);
    		this.changeData(aryId.join(","),aryName.join(","));
    	},
    	/**
    	 * 修改对应的数据。
    	 */
    	changeData(ids,names){
    		this.data[this.name]=ids;
    		this.data[this.name +"_name"]=names;
    	},
    	getJson(){
    		var ids=this.data[this.name];
    		if(!ids || ids.length==0) return '';
    		var names=this.data[this.name +"_name"];
    		var aryIds=ids.split(",");
    		var aryNames=names.split(",");
    		var aryJson=[];
    		for(var i=0;i<aryIds.length;i++){
    			var o={};
    			o.id=aryIds[i];
    			o.text=aryNames[i];
    			aryJson.push(o);
    		}
    		return JSON.stringify(aryJson);
    	},
    	valid(chkReq_){
    		if(chkReq_&& this.required){
    			if(this.jsonData&&this.jsonData!='[]'){
	    			return true;
    			} else {
	    			return false;
    			}
    		} else {
    			return true;
    		}
    	}
    },
    created(){
    	this.jsonData=this.getJson();
    	this.confJson=eval("(" +this.conf +")");
    	
    	var aryFields=this.confJson.binding.returnFields;
    	for(var i=0;i<aryFields.length;i++){
    		var field=aryFields[i];
    		if(!field.bindField) continue;
    		//构建返回字段映射。
    		this.returnFields[field.field]=field.bindField;
    	}
    }
}




