	 export default {
        name: 'rx-search',
        props: {
            value: String,
            senior:true
        },
        data(){
        	return {
        		currentValue:""
        	}
        },
        methods:{
        	blur(e){
        		this.$emit("input",this.currentValue);
        	},
        	showSenior(){
        		this.$emit("showsenior",this)
        	}
        },
        created(){
        	this.currentValue=this.value;
        }
        
 }



