<template>
    <div v-if="right=='r'" class="readOnlyBgColor">{{data[name]}}</div>
    <div class="box-custom-component" v-else-if="right=='w'">
        <label class="yd-checkbox">
            <input type="checkbox" ref="input" :checked="checked=='true'?true:false" @change="change()"/>
            <span class="yd-checkbox-icon" style="color: rgb(76, 216, 100); width: 20px; height: 20px;">
                    <i style="width: 6px; height: 12px;"></i>
            </span>
            <!--<span class="yd-checkbox-text">{{name}}</span>-->
     	</label>
    </div>    
</template>

<script type="text/ecmascript-6">
    import { RxUtil } from "@/assets/util.js";
    import { calcRight } from "@/assets/app.js";

    export default {
        name: "rx-checkbox",
        props: {
            permission: Object,
            permissionkey: String,
            conf: String,
            name: String,
            data: Object,
            readonly: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                checked:"",
        		checkReq:true
            }
        },
        computed: {
            right: function() {
               return calcRight(this);
            }
        },
        mounted() {
        	if(!this.conf||this.right=='r') return;
            this.checked = JSON.parse(this.conf).checked;
        },
        methods: {
            change() {
            	
                var inputCheckbox = this.$refs.input;
                var config=JSON.parse(this.conf);
                if(inputCheckbox.checked){
                    this.data[this.name]=config.truevalue;
                } else{
                    this.data[this.name]=config.falsevalue;
                }
            	this.checked=inputCheckbox.checked;
            },
        }

    }
</script>

<style scoped>
	
	.yd-checkbox .yd-checkbox-icon{
		margin: .12rem;
	}
	
</style>



