
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { calcRight } from "@/assets/app.js";

export default {
name : "rx-time",
components: {
PopupPicker,
Group,
Cell
},
props: {
title:String,
showDefault:{
type: Boolean,
default: true
},
value:String,
permission:Object,
permissionkey:String,
readonly: {
type: Boolean,
default: false
}
},
methods: {
onChange (val) {
var v=this.format(val);
this.selectVal=val;
this.$emit("input",v);
},
getDeault(){
var date=new Date();
var hour=date.getHours();
var minute=date.getMinutes();
var rtn=[];
rtn.push(hour +"");
rtn.push(minute +"");
return rtn;
},
format(value, name) {
return `${value[0]}:${value[1]}`;
},
init(){
var hours=[];
var seconds=[];
for(var hour=0;hour<24;hour++){
hours.push(hour +"");
}
for(var second=0;second<=59;second++){
seconds.push(second +"");
}
this.data.push(hours);
this.data.push(seconds);
}
},
data () {
return {
data: [],
selectVal:[]
}
},
computed: {
right :function () {
return calcRight(this);
}
},
created(){
this.init();

if(this.value){
var ary=this.value.split(":");
this.selectVal=ary;
}
else{
if(this.showDefault){
var v=this.getDeault();
this.selectVal=v;
}
}
}
}



