<template>
	<div v-if="right=='r'" v-html="cvalue" class="readOnlyBgColor"></div>
	<div class="box-custom-textarea" v-else-if="right=='w'">
		<yd-textarea v-model="cvalue"  :placeholder="placeholder" 
			:maxlength="maxlength" :showCounter="showCounter"></yd-textarea>
   		<!--<span class="yd-icon-must"  v-show="required&&isempty"></span>-->	
	</div>
</template>

<script>
import { calcRight } from "@/assets/app.js";

export default{
    name : "rx-textarea",
    data(){
    	return {
    		cvalue:this.value,
    		iserror:false,
    		isempty:true,
    		checkReq:true
    	}
    },
    props: {
    	value:String,
    	permission:Object,
    	permissionkey:String,
    	showClear:{
    		type: Boolean,
       		default: true
    	},
    	readonly: {
            type: Boolean,
            default: false
        },
		required: {
        	type: Boolean,
       		 default: false
    	},
        placeholder:{
        	type: String,
            default: ""
        },
        maxlength:Number,
        showCounter: {
            type: Boolean,
            default: true
        }
        
    },computed: {
    	right :function () {
			return calcRight(this);	
    	}
    },
    watch: {
    	value: function (val, oldVal){
    		this.cvalue=val;
    		this.$emit("input",val);
		},
		cvalue:function (val, oldVal){
			this.$emit("input",val);
//			if(val){
//				this.isempty=false;
//			} else{
//				this.isempty=true;
//			}
		}
    },
    methods: {
		valid(chkReq_) {
			var val=this.cvalue;
			if(chkReq_ && this.required){
				if(val.trim()==""){
					//this.iserror=true;
					return false;
				} else {
					return true;
				}
			} else {
				return true;
			}
		},
		blurHandler(e) {
			this.iserror=!this.valid(this.checkReq);
			this.onblur && this.onblur(e);
		},
		focusHandler(e) {
   			this.showClear = true;
    		this.onfocus && this.onfocus(e);
		},
    	clearInput(){
    		 this.cvalue = '';
    	}
	},
}

</script>

<style scoped>
</style>


