<template>
	<div>
		<div v-if="right=='r'" class="readOnlyBgColor">{{data[name+'_name']}}</div>
		<div class="box-custom-component" v-else-if="right=='w'">
			<!--<label class="yd-radio" v-for="json in selectJson">
		        <input 
		        	type="radio" 
		        	:checked="json.checked" 
		        	:id="json.key" 
		        	:name="name" 
		        	:value="json.value" 
		        	@change="change($event.target,json)"
	        	/>
		      Cell, 
		      XInput, 
		      XTextarea,
		      XSwitch, 
		      Calendar, 
		      XNumber, 
		      Radio, 
		      XAddress, 
		      Datetime, 
		      Selector
		    </label>-->
		    <group>
				<selector 
					:options="selectJson" 
					v-model="data[name]" 
					@on-change="onChange" 
					v-if="selectJson"
				></selector>
			</group>
		    
		    
		    
		   <!--<span class="yd-icon-must"  v-show="required&&!checkVal"></span>-->
		</div>
	</div>
	
	<!--<div :class="{readOnlyBgColor:right=='r'}">
		<div v-if="right=='r'">{{data[name+'_name']}}</div>
		<div class="box-custom-component" v-else-if="right=='w'">
			<group>
				<selector 
					:options="selectJson" 
					v-model="data[name]" 
					@on-change="onChange" 
					v-if="selectJson"
				></selector>
			</group>
			<span class="yd-icon-must" v-show="required&&emptyVal"></span>
		</div>
	</div>-->
	
	
	
</template>

<script type="text/ecmascript-6">
import { Selector, Group } from 'vux';
import { calcRight,componentAjaxJson } from "@/assets/app.js";

	export default {
		name: "rx-radiobuttonlist",
		components:{
			Selector,
			Group
		},
		props: {
			permission: Object,
			permissionkey: String,
			conf: String,
			name: String,
			data: Object,
			readonly: {
				type: Boolean,
				default: false
			},
			required: {
				type: Boolean,
				default: false
			},
		},
		data() {
			return {
				selectJson: "",
				checkVal:""
			}
		},
		computed: {
			right: function() {
				return calcRight(this);
			},
		},
		mounted() {
			if(!this.conf||this.right=='r') return;
			var conf = JSON.parse(this.conf);
			var from = conf.from;
			var params={};
			if(from == 'dic') {
				var dic = conf.dic;
				params = {dic: dic};
				componentAjaxJson(conf,params,this,data=>{
					this.selectJson=this.changeAttr("key", "value", "key", "value", data);
				});
			} else if(from == 'self') {//字典是key value形式  不需要转换
				var data = conf.data;
				this.selectJson = this.changeAttr("key", "name", "key", "value", data);
			} else if(from == 'sql'||from=='url') {
				componentAjaxJson(conf,params,this,data=>{
					var textfield = conf.textfield;
					var valuefield = conf.valuefield;
					this.selectJson=this.changeAttr(valuefield, textfield, "key", "value",data);
				});
				
			}
		},
		methods: {
			//属性转换
			changeAttr(valueField, textField, key, value, data) {
				var ary = [];
				for(var i = 0; i < data.length; i++) {
					var ob = {};
					ob[key] = data[i][valueField]; //key是实际值 value是本文值
					ob[value] = data[i][textField];
					if(this.data[this.name] == ob[key]) {
						ob.checked = true;
//						this.checkVal=ob[key];
					}
					else{
						ob.checked = false;
					}
					ary.push(ob);
				}
				return ary;
			},
			onChange(key) {
				var selectJson = this.selectJson;
				for(var i = 0; i < selectJson.length; i++) {
					if(key == selectJson[i].key) {
						this.data[this.name] = key; //实际值
						this.data[this.name + '_name'] = selectJson[i].value; //显示值
//						this.checkVal=key;
						break;
					}
				}
			},
			valid(chkReq_){
				if(chkReq_&& this.required){
					if(this.data[this.name]){
						return true;
					} else {
						return false;
					}
				} else{
					return true;
				}
				
			}
		}

	}
</script>
<style scoped>
	
</style>


