	        	<i style="width: 10px; height: 10px;"></i>
	        </span>
	        <span class="yd-radio-text">{{json.value}}
	        </span>
	      
	    </label>
	   <span class="yd-icon-must"  v-show="required&&!checkVal"></span>
	</div>
</template>

<script type="text/ecmascript-6">
	import { calcRight,componentAjaxJson } from "@/assets/app.js";

	export default {
		name: "rx-radiobuttonlist",
		props: {
			permission: Object,
			permissionkey: String,
			conf: String,
			name: String,
			data: Object,
			readonly: {
				type: Boolean,
				default: false
			},
			required: {
				type: Boolean,
				default: false
			},
		},
		data() {
			return {
				selectJson: "",
				checkVal:""
			}
		},
		computed: {
			right: function() {
				return calcRight(this);
			},
		},
		mounted() {
			if(!this.conf||this.right=='r') return;
			var conf = JSON.parse(this.conf);
			var from = conf.from;
			var params={};
			if(from == 'dic') {
				var dic = conf.dic;
				params = {dic: dic};
				componentAjaxJson(conf,params,this,data=>{
					this.selectJson=this.changeAttr("key", "value", "key", "value", data);
				});
			} else if(from == 'self') {//字典是key value形式  不需要转换
				var data = conf.data;
				this.selectJson = this.changeAttr("key", "name", "key", "value", data);
			} else if(from == 'sql'||from=='url') {
				componentAjaxJson(conf,params,this,data=>{
					var textfield = conf.textfield;
					var valuefield = conf.valuefield;
					this.selectJson=this.changeAttr(valuefield, textfield, "key", "value",data);
				});
				
			}
		},
		methods: {
			//属性转换
			changeAttr(valueField, textField, key, value, data) {
				var ary = [];
				for(var i = 0; i < data.length; i++) {
					var ob = {};
					ob[key] = data[i][valueField]; //key是实际值 value是本文值
					ob[value] = data[i][textField];
					if(this.data[this.name] == ob[key]) {
						ob.checked = true;
						this.checkVal=ob[key];
					}
					else{
						ob.checked = false;
					}
					ary.push(ob);
				}
				return ary;
			},
			change(obj,json) {
				var name = this.name;
				this.data[name] = obj.id;
				this.data[name + '_name'] = obj.value;
				for(var i=0;i<this.selectJson.length;i++){
					this.selectJson[i].checked=false;
				}
				json.checked=true;
				this.checkVal=obj.id;
			},
			valid(chkReq_){
				if(chkReq_&& this.required){
					if(this.checkVal){
						return true;
					} else {
						return false;
					}
				} else{
					return true;
				}
				
			}
		}

	}
</script>

<style scoped>
.yd-radio{
	display: flex !important;
	margin: .12rem 0;
}

