<template>

<router-link :to="link" class="yd-tabbar-item"  v-bind:class="{ noactive:!active }" v-if="type == 'link'">
    <span class="yd-tabbar-icon">
           <i  :class="icon" style="font-size: 0.6rem;"></i>
          <span class="yd-tabbar-badge" v-if="badge || badge>0">
              <span class="badge badge-danger"></span>
          </span>
          <span class="yd-tabbar-dot"  v-if="dot"></span>
    </span>
  <span class="yd-tabbar-txt">{{title}}</span>
</router-link>
<a  class="yd-tabbar-item" v-on:click="emitEvent"  v-bind:class="{ noactive:!active }" v-else-if="type == 'a'">
			<span class="yd-tabbar-icon">
             <i  :class="icon" style="font-size: 0.6rem;"></i>
         
            <span class="yd-tabbar-badge">
                <slot name="badge"></slot>
            </span>
             
            <span class="yd-tabbar-badge" v-if="badge || badge>0">
                <span class="badge badge-danger"></span>
            </span>
            <span class="yd-tabbar-dot"  v-if="dot"></span>
      </span>
      <span class="yd-tabbar-txt">{{title}}</span>
</a>
</template>������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������������
<script type="text/ecmascript-6">

 export default {
        name: 'tabbar-item',
        props:{
          icon:{type:String,required:true},
          title:{type:String,required:true},
          name:{type:String,required:true},
          link:[String, Object],
          dot:{type:Boolean},
          active:{type:Boolean,default:false},
          badge:{type:Number},
          type :{type:String}

        },
        methods:{
          emitEvent :function(){
          	this.$emit("handclick",this.name);
          }
        }
 }
</script>



