<template>
	<div v-if="right=='r'" class="readOnlyBgColor">{{value}}</div>
	<div class="box-custom-component" v-else-if="right=='w'">
	    <group   label-width="5em" v-if="right=='w'">
	      <popup-picker  :data="data" v-model="selectVal" 
	      	 @on-change="onChange" :placeholder="请选择" :display-format="format"></popup-picker>
	    </group>
	</div>

</template>

<script>
import { PopupPicker, Group, Cell } from 'vux';
import { calcRight } from "@/assets/app.js";

export default {
	name : "rx-time",
  	components: {
	    PopupPicker,
	    Group,
	    Cell
	},
	props: {
	  	title:String,
	  	showDefault:{
	  		type: Boolean,
	        default: true
	  	},
	  	value:String,
		permission:Object,
		permissionkey:String,
		readonly: {
		    type: Boolean,
		    default: false
	    }
	},
  	methods: {
	    onChange (val) {
	    	var v=this.format(val);
	    	this.selectVal=val;
	    	this.$emit("input",v);
	    },
	    getDeault(){
	    	var date=new Date();
	    	var hour=date.getHours(); 
			var minute=date.getMinutes();
			var rtn=[];
			rtn.push(hour +"");
			rtn.push(minute +"");
			return rtn;
	    },
	    format(value, name) {
	        return `${value[0]}:${value[1]}`;
	    },
	    init(){
	    	var hours=[];
	    	var seconds=[];
	    	for(var hour=0;hour<24;hour++){
	    		hours.push(hour +"");
	    	}
	    	for(var second=0;second<=59;second++){
	    		seconds.push(second +"");
	    	}
	    	this.data.push(hours);
	    	this.data.push(seconds);
	    }
  	},
  	data () {
	    return {
	      data: [],
	      selectVal:[]
	    }
  	},
  	computed: {
    	right :function () {
				return calcRight(this);	
    	}
  	},
  	created(){
    	this.init();
    	
    	if(this.value){
    		var ary=this.value.split(":");
    		this.selectVal=ary;
    	}
    	else{
    		if(this.showDefault){
    			var v=this.getDeault();
    			this.selectVal=v;
    		}
    	}
  	}
}
</script>

<style scoped>
.picker-buttons {
  margin: 0 15px;
}
</style>


