<template>
	<div id="searchPanel" class="search-panel">
		<yd-layout>
		    <yd-navbar slot="navbar" title="高级搜索"></yd-navbar>
		    <slot></slot>
    		<yd-tabbar slot="tabbar">
				<tabbar-item title="确定" name="approve" type="a" 
					v-on:handclick="handSearch()" icon="yd-icon-ok" active="true"></tabbar-item>
        		<tabbar-item title="取消" name="close" type="a" v-on:handclick="hiddenSearch()" icon="yd-icon-close" ></tabbar-item>
    		</yd-tabbar>
     	</yd-layout>
	</div>
</template>

<script>

import Vue from 'vue'
import TabItem from '@/components/common/TabItem.vue';

Vue.component(TabItem.name, TabItem);

export default {
	name: 'rx-searchpanel',
	methods:{
		handSearch(){
			this.hiddenSearch();
			var self_=this;
			setTimeout(function(){
				self_.$emit("search",self_);
			},200);
			
		},
		hiddenSearch(){
			var panel=document.getElementById("searchPanel");
   			panel.style.transform="translate(-100%)";
		},
		show(){
			var panel=document.getElementById("searchPanel");
   			panel.style.transform="translate(0)";
		}
	}
	
}
</script>

<style>

.search-panel{
	position: fixed;
	background-color: #fff;
	width: 100%;
	height: 100%;
	top:0;
	box-sizing: border-box;
	transform:translate(-100%);
 	transition: transform .2s linear;
  	border-right: 1px solid gainsboro;
  	z-index: 2000;
}

.yd-icon-close:before{
	font-size: .4rem;
	color:#696969;
}

</style>



