
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { calcRight, componentAjaxJson } from "@/assets/app.js";

export default {
components: {
Group,
Selector
},
name: "rx-combobox",
props: {
permission: Object,
permissionkey: String,
conf: String,
name: String,
data: Object,
readonly: {
type: Boolean,
default: false
},
required: {
type: Boolean,
default: false
}
},
data() {
return {
selectJson: "",
parentField: "",
linkField:"",
emptyVal:false
}
},
computed: {
right: function() {
return calcRight(this);
}
},
mounted() {
if(!this.conf || this.right == 'r') return;
var conf = JSON.parse(this.conf);
var from = conf.from;
var params = {};
if(from == 'dic') {
var dic = conf.dic;
params = {
dic: dic
};
componentAjaxJson(conf, params, this, data => {
this.selectJson = data;
}); //字典是key value形式  不需要转换
} else if(from == 'self') {
var data = conf.data;
this.selectJson = this.changeAttr("key", "name", "key", "value", data);
} else if(from == 'sql' || from == 'url') {
var sqlKey = conf.sql;
var sql_params = conf.sql_params;
var sql_parent =conf.sql_parent;
var param = {};
if(sql_params&&sql_parent) { //父联动字段
this.parentField = sql_parent;
this.linkField   =sql_params;
if(!this.data[sql_parent]) { //父联动字段为空
param[sql_params] = "---";
} else {
param[sql_params] = this.data[sql_parent];
}
}
params = {
params: param
};
componentAjaxJson(conf, params, this, data => {
var textfield = conf.textfield;
var valuefield = conf.valuefield;
this.selectJson = this.changeAttr(valuefield, textfield, "key", "value", data);
});
}
if(this.parentField) {
this.$watch("data." + this.parentField, function(val, oldVal) {
var param = {};
param[this.linkField]=val;
params = {
params: param
};
var textfield = conf.textfield;
var valuefield = conf.valuefield;
componentAjaxJson(conf, params, this, data => {
this.selectJson = this.changeAttr(valuefield, textfield, "key", "value", data);
});
});
}

if(this.data[this.name]){
this.emptyVal=false;
} else{
this.emptyVal=true;
}
},

methods: {
onChange(key) {
var selectJson = this.selectJson;
for(var i = 0; i < selectJson.length; i++) {
if(key == selectJson[i].key) {
this.data[this.name] = key; 
this.data[this.name + '_name'] = selectJson[i].value; 
this.emptyVal=false;
break;
}
}

},

changeAttr(valueField, textField, key, value, data) {
var array = [];
for(var i = 0; i < data.length; i++) {
var ob = {};
ob[key] = data[i][valueField]; 
ob[value] = data[i][textField];
array.push(ob);
}
return array;
},
valid(chkReq_){
if(chkReq_&& this.required){
if(this.emptyVal){
return false;
} else {
return true;
}
} else{
return true;
}
}

}

}



