<template>
  <div class="readOnlyBgColor">
  	{{display}}
  </div>
</template>

<script type="text/ecmascript-6">

import { calcRight } from "@/assets/app.js";

	
export default{
    name : "rx-complex",
    props: {
    	data:Object,
    	name:String,
    	permission:Object,
    	permissionkey:String,
    	readonly: {
            type: Boolean,
            default: false
        }
    },computed: {
    	right :function () {
				return calcRight(this);		
    	},
    	display:function(){
    		var tmp=this.name +"_name";
    		return this.data[tmp];
    	}
    }
}

</script>

<style scoped>

</style>



