	import { calcRight } from "@/assets/app.js";
	
	export default{
    name : "rx-date",
    props: {
    	value:String,
    	permission:Object,
    	permissionkey:String,
    	conf: String,
    	type:{
    		type: String,
            default: "date"
    	},
    	readonly: {
            type: Boolean,
            default: false
        },
        placeholder:{
        	type: String,
            default: ""
        },
    	required: {
            type: Boolean,
            default: false
        }
    },
	data() {
		return {
			emptyVal:true,
			dateType:"",
			currentValue:""
		}
	},
    computed: {
    	right :function () {
			return calcRight(this);		
    	},
    },
    watch: {
    	currentValue: function (val, oldVal){
			this.$emit('input', this.currentValue);
//			if(oldVal){
//				this.emptyVal=false;
//			}
			
		},
		value: function (val, oldVal){
			this.currentValue=val;
		}
    	
    },
    created(){
    	this.init();
    },
    methods :{
    	valid(chkReq_){
    		if(chkReq_&& this.required){
    			if(this.value&&this.value!="--"){
//	    			this.emptyVal=false;
	    			return true;
    			} else {
//	    			this.emptyVal=true;
	    			return false;
    			}
    		} else {
    			return true;
    		}
    		
    	},
    	init(){
    		var format=JSON.parse(this.conf).format;
    		var dateType="";
    		if(format=='yyyy-MM-dd HH:mm'){
    			dateType="datetime";
    		} else if(format=='yyyy-MM-dd'){
    			dateType="date";
    		} else if(format=='HH:mm'){
    			dateType="time";
    		} else{
    			dateType="datetime";
    		}
    		this.dateType=dateType;
    		
			this.currentValue=this.value;
			if(!this.currentValue){
//				if(this.required){
//	    			this.emptyVal=true;
//	    		}
	    		this.currentValue="";
			} else if(this.currentValue.length==19&&this.rigth=='w'){
				this.currentValue=this.currentValue.substr(0,16);
			}
    	}
    }
}
	



