<template>
	<div v-if="amount==0" class="nodata">
				<yd-icon slot="icon" name="nodata" ></yd-icon>
				<span class="noDataTips">无记录</span>
	</div>
</template>

<script>
	export default {
		name: 'rx-nodata',
		props:{
			amount:{
				type:Number
			}
		}
	}
</script>

<style scoped>
</style>


