import { calcRight } from "@/assets/app.js";
import {VTypes,RxUtil} from "@/assets/util.js";
	
export default{
    name : "rx-input",
    props: {
    	value:[String,Number],
    	permission:Object,
    	permissionkey:String,
    	showClear:{
    		type: Boolean,
        default: true
    	},
    	readonly: {
        type: Boolean,
        default: false
     },
      placeholder:{
        type: String,
        default: ""
      },
			required: {
        type: Boolean,
        default: false
    	},
    	/**
    	 * 验证表达式
    	 */
    	vtype:{
    		type: String,
        default: ""
    	},
    	onblur:Function,
    	onfocus:Function,
    	conf:Object
    },
    data(){
    	return {
    		currentValue: this.value,
    		iserror:false,
    		isempty:true,
    		checkReq:true
    	}
    },
    computed: {
    	right :function () {
				return calcRight(this);	
    	}
    },
    mounted(){
			this.valid(this.required);
    },
    methods: {
    	
			valid(chkReq_) {
				var val=this.currentValue;
				
				if(chkReq_ && this.required){
					if(RxUtil.isEmpty(val)){
//						this.iserror=true;
						return false;
					}
				}
				if(!this.vtype) {
//					this.iserror=false;
					return true;
				} 
				var validFunc=VTypes[this.vtype];
				if(typeof validFunc=="undefined") {
//					this.iserror=false;
					return true;
				}
				//验证
				var rtn= validFunc.valid(val);
//				this.iserror=!rtn;
				return rtn; 
			},
			blurHandler(e) {
//				this.iserror=!this.valid(this.checkReq);
				this.onblur && this.onblur(e);
			},
			focusHandler(e) {
        this.showClear = true;
        this.onfocus && this.onfocus(e);
    	},
    	clearInput(){
    		this.currentValue = '';
    		if(this.required){
//  			this.iserror=true; 
    		}
    	}
		},
    watch: {
    	currentValue: function (val, oldVal){
				this.$emit('input', this.currentValue);
				//是否为空
				this.isempty=RxUtil.isEmpty(val);
			},
			value :function(val,oldVal){
				if(val!=oldVal){
					this.currentValue=this.value;
				}
			}
    }
}




