import { RxUtil } from "@/assets/util.js";
import { calcRight,componentAjaxJson } from "@/assets/app.js";

export default {
	name: "rx-checkboxlist",
	props: {
		value: String,
		permission: Object,
		permissionkey: String,
		conf: String,
		name: String,
		data: Object,
		readonly: {
			type: Boolean,
			default: false
		},
		required: {
			type: Boolean,
			default: false
		},
	},
	data() {
		return {
			selectJson: "",
			checkKey:[],
		}
	},
	computed: {
		right: function() {
			return calcRight(this);
		}
	},
	mounted() {
		if(!this.conf||this.right=='r') return;
		var conf = JSON.parse(this.conf);
		var from = conf.from;
		var params={};
		if(from == 'dic') {
			var dic = conf.dic;
			params = {dic: dic};
			componentAjaxJson(conf,params,this,data=>{
				this.selectJson=this.changeAttr("key", "value", "key", "value", data);
			});
		} else if(from == 'self') {//自定义是key  name形式
			var data = conf.data;
			this.selectJson = this.changeAttr("key", "name", "key", "value", data);
		} else if(from == 'sql'||from=='url') {
			componentAjaxJson(conf,params,this,data=>{
				var textfield = conf.textfield;
				var valuefield = conf.valuefield;
				this.selectJson=this.changeAttr(valuefield, textfield, "key", "value",data);
			});
		} 
	},
	methods: {
		//属性转换
		changeAttr(valueField, textField, key, value, data) {
			var ary = [];
			this.checkVal=[];
			for(var i = 0; i < data.length; i++) {
				var ob = {};
				ob[key] = data[i][valueField]; //key是实际值 value是本文值
				ob[value] = data[i][textField];
				var keyText=this.data[this.name];
				if(keyText){
					var keys=keyText.split(",");//data里面的值为多个值组合  用contain判断是否包含  若包含  默认勾上
					if(keys.contains(ob[key])) {
						ob.checked = true;
						this.checkKey.push(ob[key]);
					} else {
						ob.checked = false;
					}
				} 
				ary.push(ob);
			}
			return ary;
		},
		change(json) {
			var checkedArray = this.$refs.input;
			var checkedKey=[];
			var checkedVal=[];
			for(var i=0;i<checkedArray.length;i++){
				for(var j=0;j<this.selectJson.length;j++){
					if(checkedArray[i].id==this.selectJson[j].key){
						if(checkedArray[i].checked){//选中
							this.selectJson[j].checked=true;
							checkedKey.push(checkedArray[i].id);
							checkedVal.push(checkedArray[i].value);
						} else {
							this.selectJson[j].checked=false;
						}
					break;
					}
				}
			}
			this.data[this.name] = checkedKey.toString();
			this.data[this.name + '_name'] =checkedVal.toString(); //显示值
			this.checkKey=checkedKey;
			
		},
		valid(chkReq_){
			if(chkReq_&& this.required){
				if(!this.checkKey||this.checkKey.length==0){
					return false;
				} else{
					return true;
				}
			} else {
				return true;
			}
		}
	}

}



